$text-color:rgb(5, 5, 5);
$green-color:#9BB537;
$light-text-color:#F8F8F8;

.icon-heart{
    position: absolute;
    top:18px;
    left: 214px;
    fill: $green-color;
}
.main-container{
    display: flex;
}

.side-bar{
    width: 235px;
}
.cards-and-search-container{
    max-width: 790px;
}

.popular-recipes{

    font-size: 10px;
    font-family: 'Inter';
    font-weight: 400;
    line-height: 1.4;
    color:$text-color;
    li{
        display: flex;
        max-width:100%;
        max-height:67px;

        h4{
            font-size: 14px;
            font-weight: 600;
            line-height: 1.3;
            text-transform: uppercase;
            margin-bottom: 8px;
        }
        span{
            opacity: 80%;
        }

        div{
            margin-left: 16px;
        }

        &:nth-child(-n+3){
            margin-bottom: 32px;
        }
    }

    >h4{
        font-weight: 600px;
        text-transform: uppercase;
        font-size: 24px;
        line-height: 0.85;

        margin-bottom: 32px;
    }
}

.popular-recipe-title-description{
    max-width:155px;
}

.side-bar{
    font-family: 'Inter';
    button{
        width: 174px;
        height: 50px;
        opacity:50%;
        background: none;
        outline: inherit;
        border: 1px solid rgba($text-color, 0.2);
        border-radius: 15px;
        margin-bottom: 42px;

        
        line-height: 0.82;
        font-weight: 500;
        font-size: 18px;
    }

    button:hover, button:focus{
        background-color: $green-color;
        opacity: 100%;
        border: none;
        color: $light-text-color;
    }

    .categories {

        ul{
            overflow: scroll;
            height: 416px;
            &::-webkit-scrollbar {
                width: 8px;
            }
            &::-webkit-scrollbar-thumb {
                background-color: rgba($text-color, 0.18); 
                border-radius: 12px; 
                height: 201px;
            }
        }
    
        li{
            font-weight: 500;
            font-size: 16px;
            line-height: 0.8;

        opacity: 0.3;
        color:$text-color;
        margin-top: 24px;

        &:hover, &:focus{
            color:$green-color;
            opacity: 1;
            cursor: pointer;
        }

        &:first-child{
            margin-top: 0;
        }

    } 

    margin-bottom: 64px;
    }
   
}

.cards-and-search-container{
    margin-left: 64px;
}

.search{
 .reset-filter-button{
    display: block;
    margin-left: auto;
    font-weight: 500;
    font-size: 12px;

    color: rgba($text-color,0.5);
 }
 .reset-filter-button::before{
    content: 'x ';
    color: $green-color;
 }

    ul{
        height: 74px;
        li{
            display: inline-block;
            p{
                line-height: 0.82;
                font-weight: 500;
                font-size: 14px;
                opacity: 0.5;
                margin-bottom: 8px;
            }

            margin-left: 14px;
            &:first-child{
                margin-left: 0;
            }
        }
    }
 
    input, select{
        cursor: pointer;
        height: 48px;
        opacity:50%;
        background: none;
        outline: inherit;
        border: 1px solid rgba($text-color, 0.2);
        border-radius: 15px;
        margin-bottom: 42px;

        
        line-height: 0.82;
        font-weight: 500;
        font-size: 18px;
    }

    input{
        width: 278px;
    }
    select[name="time"]{
        padding-left: 18px;
            width: 125px;
    }
    select[name="area"]{
        padding-left: 18px;
        width: 141px;
    }
    select[name="ingredients"]{
        padding-left:18px;
        width: 188px;
    }

    button{
        border: none;
        background: none;
        margin-top: 0;
        margin-top:16px;
    }
}

.gallery-container{
    ul{
        display: flex;
        flex-wrap: wrap;

        >li{
            position: relative; 
            margin-left: 20px;
            margin-bottom: 16px;
            
            &:nth-child(3n+1){
                margin-left: 0;
            }
            img{
            border-radius: 15px;
            }

            
        }

       
    }
    
}

.description-rating-container{

    color: $light-text-color;
    padding: 16px;
    position: absolute;
    font-family: 'Inter';
    top:calc(166px - 16px);
    left: 0;

    p{
       font-weight: 600; 
       font-size: 14px;

       line-height: 0.7;
       text-transform: uppercase;
       margin-bottom: 8px;
        
    }
    span{
        display: block;
        font-weight: 400;
        font-size: 12px;
        line-height: 1.3;
        margin-bottom: 16px;
    }
    .rating{
        display: flex;
        align-items: center;
        div{
            display: flex;
            align-items: center;
        }
        ul{
           margin-top: 0;
 
            li{
                padding: 2px;
            margin:0;
            svg{
                fill: #EEA10C;
            }
            
        } 
        
        }
       button{
            background-color: $green-color;
            padding: 8px, 16px;
            height: 31px;
            width: 93px;
            border-radius: 8px;
            border: none;
            margin-left: 9px;

            font-weight: 500;
            font-size: 12px;
            line-height: 1.2;

        }
    }
}

.pagination{

    margin-top: calc(65px - 16px);
    display: flex;
    align-items: center;
    justify-content: center;
    li{
        display: inline-block;
       padding: 11px 16px;
       width:44px;
       height: 44px;
       border: 1px solid rgba($text-color, 0.3);
       border-radius: 50%;
    }
    .page:hover,.page:focus{
        background-color: $green-color;
        color: $light-text-color;
        border: 1px solid rgba($green-color,1);
        cursor: pointer;
    }
    .more-pages{
        padding-bottom: 9px;
        padding-left: 15px;
        margin-bottom: 0;
    }
    .to-start-button,.back-by-one-button,.go-by-one-button,.to-end-button{
        width:44px;
        height: 44px;
        background-color: $green-color;
        color:$text-color;
        border: none;
        border-radius: 50%;
    }

    >button:hover, >button:focus{
        background-color: $text-color;
        color: rgb(90, 90, 90);
    }

    .back-by-one-button{
        margin-left: 8px;
        margin-right: 24px;
    }
    .go-by-one-button{
        margin-right: 8px;
        margin-left: 24px;
    }


}
