/* MAIN */
.header{
    padding-top: 24px;
    @media screen and (min-width: 768px) {
        padding-top: 28px;
    }
    @media screen and (min-width: 1280px) {
        padding-top: 40px;
        
    } 
    }

    .navigation {
        display: none;
        @media screen and (min-width: 768px) {
            display: inline;
            position: relative;
    }
    }
    .navigation a {
        position: relative;
    }
    .navigation a::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: var(--green); /* Цвет полоски */
      transform: scaleX(0);
      transition: transform 0.3s ease-in-out;
    }
    .navigation a:hover::before,
    .navigation a:focus::before {
      transform: scaleX(1);
    }
    .header-nav-links{
        color: #050505;
        font-size: 12px;
        font-weight: 500;
        font-size: 12px;
        
    }
    .header-nav-links:hover,
    .header-nav-links:focus{
    color: #9BB537;
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.btn-home{
    margin-right: 16px;
}

.logo-first {
    color: #9BB537;
    font-size: 16px;
    font-weight: 600;
    line-height: calc(18/16);
    letter-spacing: -0.64px;
    @media screen and (min-width:768px){
        font-size: 18px;
        line-height: calc(20/18);
        letter-spacing: -0.72px;
    }
}

.logo-second {
    color: #050505;
    font-size: 16px;
    font-weight: 600;
    line-height: calc(18/16);
    letter-spacing: -0.64px;
    @media screen and (min-width:768px) {
        font-size: 18px;
        line-height: calc(20/18);
        letter-spacing: -0.72px;
    }
}

.header-items {
    display: flex;
    align-items: center;
    @media screen and (min-width:768px) {
        justify-content: space-between;
    }
}

.header-icons {
    display: flex;
    align-items: center;
    gap: 14px;
    margin-left: auto;
    @media screen and (min-width:768px) {
        margin-left: 0;
    }
}

.header-order-btn{
    display: flex;
    align-items: center;
    justify-content: center;
}

.mobile-menu-btn {
    border: none;
    padding: 0;
    margin: 0;
    @media screen and (min-width: 768px) {
        display: none;
    }
}

.mobile-icon{
    stroke: #050505;
}

.header-switch {
    display: none;
    @media screen and(min-width:768px) {
        position: relative;
        display: flex;
        width: 47px;
        height: 20px;
        border-radius: 10px;
        box-shadow: 7px 5px 15px 0px #A09EA066;
    }
  }

  .header-switch input {
    display: none;
  }

  .header-slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #CECDCD;
    transition: 0.4s;
    border-radius: 34px;
    box-shadow: 4px 4px 15px rgba(243, 243, 243, 0.25);
}

.header-slider:before {
    position: absolute;
    content: "";
    height: 17px;
    width: 17px;
    left: 2px;
    bottom: 2px;
    background-color: #F8F8F8;
    transition: 0.4s; 
    border-radius: 50%;
  }

  .header-switch input:checked + .header-slider {
    background: linear-gradient(#9BB537, #9BB537);
    box-shadow: 4px 4px 15px rgba(243, 243, 243, 0.25);
  }
  
  .header-switch input:checked + .header-slider:before {
    box-shadow: 4px 4px 15px rgba(243, 243, 243, 0.25);
    transform: translateX(26px);
  }

  /* mobile-menu*/

  .mobile-menu {
    background-color: #9BB537;
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    transform: translateX(-100%);
    transition: transform 0.3s ease-in-out;

    @media screen and (min-width: 768px) {
        display: none;
    }
}

.mobile-menu.open {
    transform: translateX(0);
}

.mobile-menu-btn-close {
    position: absolute;
    top: 20px;
    right: 20px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: transform 0.3s ease-in-out;
}
.mobile-menu-btn-close .mobile-menu-close {
    transition: transform 0.3s ease-in-out;
}
.mobile-menu-btn-close:hover {
    transform: rotate(45deg);
}
.mobile-menu-btn-close:hover .mob-menu-close {
    transform: scale(1.2);
}

.mobile-menu-close-icon{
    stroke: white;
}

.mobile-menu-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 16px;
    padding-top: 200px;
    padding-bottom: 566px;
}

.header-list-item .main-page{
    color: #FFF;
    font-size: 12px;
    font-weight: 500;
    justify-content: center;
}

.header-list-item{
    color: #FFF;
    font-size: 12px;
    font-weight: 500;
    justify-content: center;
}

.active-header-list-item{
    color: #050505;
}

.switch {
    position: absolute;
    width: 46px;
    height: 20px;
    bottom: 30px;
    left: 20px;
}

.switch input {
    display: none;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #CECDCD;
    transition: 0.4s;
    border-radius: 34px;
}

.slider:before {
    position: absolute;
    content: "";
    height: 15px;
    width: 15px;
    left: 4px;
    bottom: 3px;
    background-color: #F8F8F8;
    transition: 0.4s;
    border-radius: 50%;
}

input:checked+.slider {
    background: #9BB537;
}

input:checked+.slider:before {
    transform: translateX(26px);
    box-shadow: 4px 4px 15px rgba(243, 243, 243, 0.25);
}

.current::after{
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #9BB537;
}

.nav-link-active{
    color: #9BB537;
}

.shopping-icon-use{
    fill: #F8F8F8;
    stroke: #050505;
}

