.loader {
  width: 120px;
  height: 80px;
  background: linear-gradient(174deg, rgba(0, 0, 0, 0) 49%, #000 50%, rgba(0, 0, 0, 0) 51%);
  position: absolute;
  top: 90px;
  transform: rotate(-90deg);
}

.loader:after, .loader:before {
  content: "";
  width: 35px;
  height: 35px;
  box-sizing: border-box;
  border: 4px dashed #fff;
  border-radius: 100%;
  animation: tape 4s linear infinite;
  display: block;
  position: absolute;
  bottom: 49px;
  transform: rotate(0deg);
}

.loader:before {
  right: -14px;
  box-shadow: 0 0 0 4px #fff, 0 0 0 34px #000, 0 0 5px 34px rgba(0, 0, 0, .333);
}

.loader:after {
  left: -13px;
  box-shadow: 0 0 0 4px #fff, 0 0 0 65px #000, 0 0 5px 65px rgba(0, 0, 0, .333);
}

@keyframes tape {
  0% {
    transform: rotate(0deg)scale(.4);
  }

  100% {
    transform: rotate(-360deg)scale(.4);
  }
}

.loader-is-hidden {
  display: none;
}

